import React from 'react';
import './header.css';
import logo from '../../../media/svg/header-logo.svg';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import classNames from 'classnames';

interface HeaderState {
    menuOpened: boolean;
}
class Header extends React.Component<any, HeaderState> {
    constructor(props: any) {
        super(props);
        this.state = {menuOpened: false};
    }
    render() {
        return (
            <header className={classNames("site-header", this.props.className)}>
                <button onClick={() => this.setState(state => ({menuOpened: !state.menuOpened}))} className="open-menu-button">
                <a data-v-a0b5e022="" className="is-menu">
                    <svg data-v-a0b5e022="" width="32" height="32" fill="none" xmlns="http://www.w3.org/2000/svg" data-v-63e8790d="" className="menu__icon">
                        <path data-v-a0b5e022="" d="M26 9a1 1 0 00-1-1H7a1 1 0 00-1 1v1.101a1 1 0 001 1h18a1 1 0 001-1V9zM26 15.449a1 1 0 00-1-1H7a1 1 0 00-1 1v1.1a1 1 0 001 1h18a1 1 0 001-1v-1.1zM6 21.899a1 1 0 011-1h11.995a1 1 0 011 1V23a1 1 0 01-1 1H7a1 1 0 01-1-1v-1.101z" fill="#23303D" data-v-63e8790d=""></path>
                    </svg>
                    <div data-v-a0b5e022="" className="menu-title">Menu</div>
                </a>
                </button>
                <ul data-v-a0b5e022="" className={classNames("sub-menu", this.state.menuOpened && "show")}>
                   <li data-v-a0b5e022=""><a data-v-a0b5e022="" href="/job-openings">Job openings</a></li>
                   <li data-v-a0b5e022=""><a data-v-a0b5e022="" href="/candidates">All Candidates</a></li>
                   <li data-v-a0b5e022=""><a data-v-a0b5e022="" href="/login">My Favorites</a></li>
                   <li data-v-a0b5e022=""><a data-v-a0b5e022="" href="/blog">Blog</a></li>
                   <li data-v-a0b5e022=""><a data-v-a0b5e022="" href="/#jobitt-safe">Jobitt Safe</a></li>
                </ul>
                <a className="site-logo-link" href="/">
                    <img className="site-logo" src={logo} alt="site-logo" />
                </a>
                {/* <a data-v-a0b5e022="" href="/#jobitt-safe" className="header-safe">
                    JOBITT SAFE
                </a> */}
                <div className="login-link-wrapper">
                    <a data-v-a0b5e022="" href="/login" className="login-link">Login / Sign Up</a>
                </div>
            </header>
        )
    }
}

export default Header;