import classNames from "classnames";
import React from "react";
import PDFProps from '../PDFprops';
import TemplatePreview from "../template-preview/TemplatePreview";
import UserInfo from "../user-info/UserInfo";
import './creating-section.css';
const TEMPLATE_FOR_DEMO = `




<style>
<style>
    .template__body * {
        margin: 0;
        padding: 0;
        font-family: sans-serif;
    }
    . template__body p {
        font-family: sans-serif;
    }
    .template__body {
        padding: 5em;
        background-color: #ffffff;
        --main-text-color: #2f4455;
        color: var(--main-text-color);
        font-family: sans-serif;
    }
    .template__name {
        display: block;
        font-size: 4em;
    }
    .template__light-text-info-block {
        font-size: 1.5em;
    }
    .template__links-container {
        margin-top: 1em;
        font-size: 1.5em;
    }
    .template__links-container a {
        text-decoration: none;
        color: var(--main-text-color);
    }
    .template__section-header {
        overflow: hidden;
        display: block;
        margin-top: 1em;
        font-size: 2em;
        white-space: nowrap;
    }
    .template__section-header::after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 0.1em;
        margin-left: 0.5em;
        background: var(--main-text-color);
    }
    .template__image-wrapper {
        width: 20em;
        height: 20em;
        display: inline-block;
    }
    .template__contacts {
        display: inline-block;
    }
    .template__main-info-wrapper {
        display: flex;
        justify-content: space-between;
    }
    .template__light-text-info-block {
        margin-top: 0.5em;
    }
    .template__work-experience-list {
            font-family: sans-serif;
            margin-top: 30px;
            list-style: none;
        }
        .template__work-experience__main-info-wrapper {
            display: flex;
            justify-content: space-between;
            flex-direction: column;
        }
        .template__work-experience-list__item {
            margin-top: 20px;
        }
        .template__work-experience__title {
            font-size: 2em;
        }
        .template__work-experience__company {
            font-size: 1.4em;
            color: var(--main-text-color);
        }
        .template__work-experience__about {
            font-size: 1.6em;
            margin-top: 16px;
            color: #7e7e7e;
        }
        .template__work-experience__period {
            font-size: 1.2em;
            color: var(--main-text-color);
        }
        .template__skills-list {
            margin-top: 35px;
            list-style: none;
            font-family: sans-serif;
        }
        .template__skills-list__item {
            width: 100%;
        }
        .template__skills-list__title {
            display: inline-block;
            font-size: 1.8em;
            width: 10em;
        }
        .template__skills-list__process {
            margin-left: 20px;
            display: inline-block;
            width: 20em;
            height: 1em;
            background-color: #7e7e7e;
        }
        .template__skills-list__process-bar {
            height: 100%;
            background-color: var(--main-text-color);
        }
        .template__image-wrapper {
            overflow: hidden;
            border-radius: 100%;
            clip-path: circle(50% at 50% 50%);
            background-color: var(--main-text-color);
        }
        .template__image-wrapper img {
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
</style>
    <div class="template__body">
        <div class="template__main-info-wrapper">
            <div>
                <h1 class="template__name">{{name}}</h1>
                <div class="template__contacts">
                    <p class="template__light-text-info-block">{{phone}}</p>
                    <p class="template__light-text-info-block">{{email}}</p>
                    <p class="template__light-text-info-block">{{telegram}}</p>
                    <p class="template__light-text-info-block">{{skype}}</p>
                    <p class="template__light-text-info-block">{{country}} - {{city}}</p>
                </div>
            </div>
            {{#if img}}
            <div class="template__image-wrapper">
                <img src="{{img}}" alt="">
            </div>
            {{/if}}
        </div>
        <div class="template__links-container">
            {{#if links.linkedInLink}}
                <a href="{{links.linkedInLink}}">LinkedIn</a>
            {{/if}}

            {{#if links.gitHubLink}}
                <a href="{{links.gitHubLink}}">GitHub</a>
            {{/if}}

            {{#if links.portfolioLink}}
                <a href="{{links.portfolioLink}}">Portfolio</a>
            {{/if}}
        </div>
        {{#if about}}
        <h2 class="template__section-header">About</h2>
        <div class="template__light-text-info-block">
            {{{this.about}}}
        </div>
        {{/if}}
        {{#if experience}}
        <h2 class="template__section-header">Work experience</h2>
        <ul class="template__work-experience-list">
        {{#each experience}}
        <li class="template__work-epxerience-list__item">
            <div class="template__work-experience__main-info-wrapper">
                <div class="template__work-experience__title-wrapper">
                    <h4 class="template__work-experience__title">{{this.role}}</h4>
                    <p class="template__work-experience__company">{{this.companyName}}</p>
                </div>
                <div class="template__work-experience__period-wrapper">
                    <p class="template__work-experience__period">{{this.startMonth}} {{this.startYear}} - {{this.endMonth}} {{this.endYear}}</p>
                </div>
            </div>
            <p class="template__work-experience__about">{{{this.description}}}</p>
        </li>
        {{/each}}
        </ul>
        {{/if}}
        {{#if skills}}
        <h2 class="template__section-header">Skills</h2>
        <ul class="template__skills-list">
            {{#each skills}}
            <li class="template__skills-list__item">
                <span class="template__skills-list__title">{{this.skillName}}</span>
                <div class="template__skills-list__process">
                    <div style="width: {{this.level}}%" class="template__skills-list__process-bar"></div>
                </div>
            </li>
        {{/each}}
        </ul>
        {{/if}}
        
        <div class="template__links-container">
            {{#each links.customLinks}}
            <a href="{{this.link}}">{{this.linkName}}</a>
            {{/each}}
        </div>
    </div>
`
const {URL} = require('../../../API.json');

interface CreatingSectionProps {
    templateId: string;
    onFinish: () => void;
}


interface CreatingSectionState {
    userData: PDFProps;
    template: string;
    showTemplate: boolean;
}


export default class CreatingSection extends React.Component<CreatingSectionProps, CreatingSectionState> {
    templateUpdate: null | (() => void);
    constructor(props: CreatingSectionProps) {
        super(props);
        this.state = {userData: 
            {
                name:      undefined,
                img:       undefined,
                phone:     undefined,
                email:     undefined,
                skype:     undefined,
                telegram:  undefined,
                country:   undefined,
                city:      undefined,
                links: {
                    linkedInLink:  undefined,
                    gitHubLink:    undefined,
                    portfolioLink: undefined,
                    customLinks:   []
                },
                about: '',
                experience: {},
                skills: {}
            },
            template: '',
            showTemplate: false
        };
        this.setUserData = this.setUserData.bind(this);
        this.showTemplate = this.showTemplate.bind(this);
        this.hideTemplate = this.hideTemplate.bind(this);
        this.initTemplate = this.initTemplate.bind(this);
        this.templateUpdate = null;
    }
    componentDidMount() {
        // fetch(URL + '/0/0.hbs').then(data => data.text()).then(data => this.setState({template: data}));
        this.setState({template: TEMPLATE_FOR_DEMO})
    }
    setUserData(userData: PDFProps) {
        this.setState({userData});
        this.templateUpdate && this.templateUpdate();
    }
    showTemplate() {
        this.setState({showTemplate: true});
    }
    hideTemplate() {
        this.setState({showTemplate: false});
    }
    initTemplate(templateUpdate: () => void) {
        this.templateUpdate = templateUpdate;
    }
    render() {
        return <section className="creating-section">
            {!this.state.showTemplate && <button onClick={this.showTemplate} className="show-template-button"></button>}
            <UserInfo setUserData={this.setUserData} userData={this.state.userData} />
            <TemplatePreview onFinish={this.props.onFinish} init={this.initTemplate} hide={this.hideTemplate} className={classNames(this.state.showTemplate && "shown")} id={this.props.templateId} userData={this.state.userData} template={this.state.template} />
        </section>
    }
}