import CommonProps from '../../common-props';
import classNames from "classnames";
import React from "react";
import TextInput from "../text-input/TextInput";
import TitledInputWrapper from "../titled-input/TitledInput";

interface CustomLinkProps extends  CommonProps{
    linkName: string, id: string, link: string, setValue: any;
}
interface CustomLinksState {
    linkName: string;
    link: string;
}
export default class CustomLink extends React.Component<CustomLinkProps, CustomLinksState> {
    id: string;
    constructor(props: CustomLinkProps) {
        super(props);
        const {link, linkName, id} = props;
        this.state = {
            linkName,
            link
        }
        this.id = id;
        this.setValue = this.setValue.bind(this);
    }
    setValue(options: {link?: string, linkName?: string}) {
        console.log('SET!');
        
        this.props.setValue({id: this.id, ...options});
    }
    render() {
        const {style, className} = this.props;
        const {link, linkName} = this.state;
        return <div style={{...style}} className={classNames("custom-link-wrapper", className)}>
            <TitledInputWrapper className="link-input-wrapper" title="Custom link" protectedSign={false}>
                <>
                    <TextInput onInput={(event: React.SyntheticEvent) => {this.setValue({linkName: (event.target as HTMLInputElement).value})}} className="link-name-input" value={linkName} placeholder="Link name" name="unused"/>
                    <TextInput onInput={(event: React.SyntheticEvent) => {this.setValue({link: (event.target as HTMLInputElement).value})}} className="link-value-input" value={link} placeholder="Custom link" name="unused" />
                </>
            </TitledInputWrapper>
        </div>
    }
}