import React from "react";
import HtmlDisplay from "../../atoms/html-display/HtmlDisplay";
import PDFProps from "../PDFprops";
import Handlebars from "handlebars";
import RectButton from "../../atoms/rect-button/RectButton";
import {savePDF} from '@progress/kendo-react-pdf';
import classNames from "classnames";
import './template-preview.css';
const {URL} = require('../../../API.json');
interface TemplateProps {
    id: string;
    userData: PDFProps;
    template: string;
    className?: string;
    hide?: () => void;
    init?: (callback: () => void) => void;
    onFinish?: () => void;
}
interface TemplateState {
    wrapperWidth: number;
    wrapperHeight: number;
    fontSize: number;
    whitespaceWidth: number;
    whitespaceHeight: number;
    pagesCount: number;
    currentPage: number;
}
interface TemplateDataLinks {
    linkedInLink:  string | undefined,
    gitHubLink:    string | undefined,
    portfolioLink: string | undefined,
    customLinks:   {id?: string, link: string | undefined, linkName: string | undefined}[]
}
interface TemplateData {
    name:      string | undefined,
    img:       string | undefined | ArrayBuffer,
    phone:     string | undefined,
    email:     string | undefined,
    skype:     string | undefined,
    telegram:  string | undefined,
    country:   string | undefined,
    city:      string | undefined,
    links: TemplateDataLinks,
    about: string | undefined,
    experience: {
        
            role:        string | undefined,
            companyName: string | undefined,
            startMonth:  string | undefined,
            startYear:   string | undefined,
            endMonth:    string | undefined,
            endYear:     string | undefined,
            description: string | undefined,
            id?:         string | undefined
        
    }[] | undefined
    skills: 
        {
            id:                string,
            skillName:         string,
            yearsOfExperience: number | undefined,
            level:             number,
            place: number
        }[] | undefined
}
const addProtocolToLink = function(link: string) : string {
    if (link.indexOf('http://') === -1 || link.indexOf('https://') === -1) {
        return 'https://' + link;
    }
    return link;
}
export default class TemplatePreview extends React.Component <TemplateProps, TemplateState> {
    resumeContent: any;
    wrapperRef: React.RefObject<HTMLDivElement>;
    constructor(props: TemplateProps) {
        super(props);
        this.wrapperRef = React.createRef();
        this.resumeContent = React.createRef();
        this.state = {wrapperWidth: 0, wrapperHeight: 0, fontSize: 0, whitespaceWidth: 0, whitespaceHeight: 0, pagesCount: 1, currentPage: 1};
        this.resize = this.resize.bind(this);
        this.onContentUpdate = this.onContentUpdate.bind(this);
        this.toPreviousPage = this.toPreviousPage.bind(this);
        this.toNextPage = this.toNextPage.bind(this);
    }
    componentDidMount() {
        window.addEventListener('resize', this.resize);
        this.resize();
        if (this.props.init) {
            this.props.init(this.onContentUpdate);
        }
    }
    componentDidUpdate() {
        // const A4aspect = 297/210;
        const content = this.resumeContent.current as HTMLDivElement;
        const {whitespaceWidth, whitespaceHeight, fontSize} = this.state;
        // const whitespaceHeight = whitespaceWidth * A4aspect;
        if (!whitespaceHeight) return;  
        console.log('update', whitespaceHeight);
        
        const childDiv:HTMLDivElement | null = content.querySelector('div');
        if (!childDiv) return;
        let page = 1;
        childDiv.querySelectorAll('*').forEach(el => {
            const em = fontSize;
            content.style.setProperty('--margin', em * 28 + 'px');
            
            el.classList.remove('template__page-break');
            if (!this.inspectNode(content, el as HTMLElement, whitespaceHeight * page - em * 18)) {
                el.classList.add('template__page-break');
                (el as HTMLElement).style.setProperty('--self-margin',  el.clientHeight/2 + 'px');
                console.log(el);
                console.log(content);
                page++;
            }
        });
        
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.resize);
    }
    resize() {
        this.onContentUpdate();
    }
    onContentUpdate() {

        const whitespace:HTMLDivElement | undefined | null = this.wrapperRef.current?.querySelector('.template-whitespace');
        const wrapperHeight = this.wrapperRef.current?.clientHeight || 0;
        const wrapperWidth = this.wrapperRef.current?.clientWidth || 0;
        const fontSize = window.innerWidth > window.innerHeight ? wrapperHeight/350 : wrapperWidth/300;
        const content:Element | undefined = whitespace?.children[0];
        const contentHeight: number = content ? content?.clientHeight : 0;
        const whitespaceWidth:number = fontSize * 210;
        const whitespaceHeight:number = fontSize * 291;
        const pagesCount: number = !!contentHeight && !!whitespaceHeight ? Math.ceil(Math.max(contentHeight / whitespaceHeight, 1)) : 1;
        console.log('pages count');
        
        console.log(contentHeight / whitespaceHeight);
        
        this.setState(state => ({
            whitespaceWidth,
            whitespaceHeight,
            wrapperWidth: this.wrapperRef.current?.clientWidth || 0,
            wrapperHeight: this.wrapperRef.current?.clientHeight || 0,
            fontSize,
            pagesCount: Number.isNaN(pagesCount) ? 1 : pagesCount,
            currentPage: state.currentPage > pagesCount ? pagesCount : state.currentPage
        }))
    }
    toPreviousPage() {
        this.setState(state => ({currentPage: state.currentPage > 1 ? state.currentPage - 1 : state.currentPage}));
    }
    toNextPage() {
        this.setState(state => ({currentPage: state.currentPage < state.pagesCount ? state.currentPage + 1 : state.pagesCount}));
    }
    getElementRelativePosition(parent: HTMLElement, child: HTMLElement) {
        var bodyRect = parent.getBoundingClientRect(),
        elemRect = child.getBoundingClientRect(),
        offset   = elemRect.top - bodyRect.top;
        
        return offset;
    }
    inspectNode(parent: HTMLElement, node: HTMLElement, positionY: number) {
        if (positionY - node.offsetHeight/2 < 0) return true;
        return this.getElementRelativePosition(parent, node) < positionY - node.offsetHeight/2;
    }
    
    render() { 
        const skills = Object.entries(this.props.userData.skills)
        .map(entry => {const level = entry[1].level;
        
        return Object.assign(JSON.parse(JSON.stringify(entry[1])), {level: level /5 * 100})})
        .sort((a, b) => a.place - b.place)
        const experience = Object.values(this.props.userData.experience);
        const about = this.props.userData.about;
        const links = {...this.props.userData.links} as {
            linkedInLink:  string | undefined,
            gitHubLink:    string | undefined,
            portfolioLink: string | undefined,
            customLinks:   {id?: string, link: string | undefined, linkName: string | undefined}[]
        };
        links.gitHubLink = links.gitHubLink?.length ? addProtocolToLink(links.gitHubLink) : undefined;
        links.linkedInLink = links.linkedInLink?.length ? addProtocolToLink(links.linkedInLink) : undefined;
        links.portfolioLink = links.portfolioLink?.length ? addProtocolToLink(links.portfolioLink) : undefined;
        links.customLinks = links.customLinks.map(el => ({
            id: el.id,
            link: el.link && !!el.link.length ? addProtocolToLink(el.link) : undefined,
            linkName: el.linkName && !!el.linkName.length ? el.linkName : undefined
        }))
        const data:TemplateData = {...this.props.userData,
            ...{skills: !!skills.length ? skills : undefined},
            ...{experience: !!experience.length ? experience : undefined},
            ...{about: !!about.length ? about : undefined},
            links
        };
        
        
        
        const {pagesCount, currentPage, wrapperWidth, wrapperHeight, fontSize, whitespaceHeight} = this.state;
        // console.log('render', whitespaceHeight);
        
        
        const whitespaceStyles:React.CSSProperties = {fontSize: fontSize + 'px'};
        return (
            <div ref={this.wrapperRef} className={classNames(this.props.className, "preview-wrapper")}>
                <button onClick={this.props.hide} className="close-template-button"></button>
                <div className="template-controls">
                    <button onClick={this.toPreviousPage} className="template-controls__button previous-template">&larr;</button>
                    <p className="template-controls__text medium-font">{currentPage} / {pagesCount}</p>
                    <button onClick={this.toNextPage} className="template-controls__button next-template">&rarr;</button>
                </div>
                <div style={{...whitespaceStyles}} className="template-whitespace">
                    <div style={{transform: `translateY(-${(currentPage - 1) * whitespaceHeight}px)`}}>
                        <HtmlDisplay style={{fontSize: '250%'}} ref={this.resumeContent}>
                            {Handlebars.compile(this.props.template)(data)}
                        </HtmlDisplay>
                    </div>
                </div>
                <RectButton className="download-btn" onClick={() => {
                    
    

                    const content = this.resumeContent.current as HTMLDivElement;
                    (content as HTMLDivElement).style.setProperty('--margin', 'null');
                    // content.style.width = '30cm';
                    // content.style.fontSize = '0.15cm';

                    savePDF(this.resumeContent.current, {paperSize: 'A4'}, () => {
                        content.style.width = '';
                        content.style.fontSize = '250%';
                        this.onContentUpdate();
                        // this.props.onFinish && this.props.onFinish();
                    });

                    

                }}>download resume</RectButton>
            </div>
        )
    }
}