import React from 'react';
import RectButton from '../../atoms/rect-button/RectButton';
import './finish-creating-section.css';

export default class FinishCreatingSection extends React.Component {
    constructor(props: any) {
        super(props);
    }
    render() {
        return (
            <section className="finish-section">
                <div className="finish-text-wrapper">
                    <h2 className="tanks-text t-center">thank <span className="rect-text">you</span></h2>
                    <h3 className="question-text t-center">Thanks for creating<br/>your resume at our platform.<br/>do you want to join to J<span className="accent">o</span>bitt community?</h3>
                    <RectButton className="upload-btn">upload your resume to jobitt</RectButton>
                </div>
            </section>
        )
    }
}


